.links a {
  margin-right: 10px;
  color:   yellow;
  text-decoration: none;
  position: relative;
}

.links a.highlight::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 0;
  height: 3px;
  background-color: red; /* Change the color of the underline here */
  z-index: -1;
  transition: width 0.4s ease; /* Add transition property */
}

.links a.highlight:hover {
  /* Change the color of the highlighted word on hover */
  filter: brightness(300%);
}

.links a.highlight:hover::before {
  width: 100%; /* Expand the width to 100% on hover */
  background-color: rgb(247, 9, 9); /* Change the color of the underline on hover */
}

.links a.highlight:hover ~ a {
  color: #24AC61; /* Change the color of the remaining words on hover */
}



@keyframes underline-animation {
  from {
    width: 0; /* Start with width 0 */
  }
  to {
    width: 100%; /* End with width 100% */
  }
}




.navbar a {
  color: #24AC61;
  text-decoration: none;
  margin: 25px;
  font-size: 30px;
}

.toggleButton {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.toggleButton svg {
  font-size: 50px;
}

.toggleButton button {
  margin-right: 20px;
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
}

#open {
  height: 100vh;
 
 
}

#open .links {
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 100px; 
  
 

}


#open a {
  width: 100%;
  text-align: center;
  
}
#open .links a{
 background-color: rgb(130, 31, 196);
 height: 100px;

}

@media only screen and (max-width: 900px) {
  .navbar a {
      width: 70px;
  }

}

@media only screen and (max-width: 600px) {
  .toggleButton {
      display: flex;
  }
  #close a {
      display: none;
  }
}

@media only screen and (min-width: 600px) {
  .toggleButton {
      display: none;
  }
  .hiddenLinks {
      display: none;
  }
  #open {
      height: 100px;
      
  }
  #open .links {
      flex-direction: row;
  }



 
}